.box {
  background-color: rgb(86, 86, 86);
  padding: 15px;
  border-radius: 10px;
  color: #ffff;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  font-size: 10px;
  font-family: sans-serif;
  -ms-transform: skew(170deg);
  /* Support untuk IE 9 */
  -webkit-transform: skew(170deg);
  /* support untuk Safari */
  transform: skew(170deg);
  /* default syntax */
  height: 120px;
  width: 100%;
}

.modalPilih {
  display: flex;
  flex-direction: column;
  border-radius: 4vw;
  background-color: var(--popup-background);
  background-image: var(--gradient-content);
  padding: 1.5vw 4vw;
  text-align: left;
  width: 98%;
  height: auto;
  max-height: 85vh;
  position: fixed;
  overflow: auto;
  z-index: 90;
  cursor: default;
  opacity: 0;
  animation: slideIn 0.5s ease-in-out forwards;

  @media (min-width: 1081px) {
    top: 3%;
    left: 8%;
  }

  @media (max-width: 1080px) {
    top: auto;
    bottom: 4%;
    left: 1%;
  }
}

.modalPilih .containerfilter {
  width: 100%;
  display: flex;
  justify-content: space-between;
  column-gap: 0.75vw;
  margin-bottom: 5px;

  @media (min-width: 1017px) {
    flex-direction: row;
  }

  @media (max-width: 1016px) {
    flex-direction: column;
  }
}

.modalPilih .containerfilter input {
  @media (min-width: 1017px) {
    height: 3vw;
  }

  @media (max-width: 1016px) {
    height: 25px;
  }
}



.modalConfirm {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 2vw;
  background-color: var(--popup-background);
  /* background-color: magenta; */
  padding: 1vw 2vw;
  width: 35%;
  max-height: 60%;
  position: fixed;
  top: 20vh;
  left: 32.5%;
  overflow: auto;
  z-index: 22;
}

.modalPilih .tabView::-webkit-scrollbar {
  display: none;
}

.modalKeyboardNumeric {
  /* background-color: mediumvioletred; */
  width: 30%;
  position: fixed;
  bottom: 2.7vh;
  left: 35%;
  z-index: 100;
  font-size: 2vw;
}

.modalKeyboard {
  /* background-color: mediumvioletred; */
  width: 70%;
  position: fixed;
  bottom: 2.7vh;
  left: 15%;
  z-index: 100;
