  flex-direction: column;
  border-radius: 0.75vw;
  background-color: var(--popup-background);
  background-image: var(--gradient-content);
  padding: 1.5vw 4vw;
  text-align: left;
  width: 85%;
  height: auto;
  max-height: 92.5vh;
  position: fixed;
  overflow: auto;
  z-index: 20;
