}

/* .modal .tabView::-webkit-scrollbar {
  display: none;
} */

.modaldetail {
  display: flex;
  flex-direction: column;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: var(--popup-background);
  /* padding: 2rem; */
  width: 100%;
  overflow: auto;
  top: 5vh;
  height: 80%;
  left: 20%;
  z-index: 3;
}

.modal .buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 1vw;
}

.modal .input {
  width: 75%;
  height: 1vw;
}

.modal .input input {
  width: 100%;
  font-size: 1vw;
}

.modal .title {
  width: 25%;
  height: 1vw;
  font-size: 1vw;
}

.modalpilih .form {
  /* background-color: aqua; */
  width: 70%;
  /* z-index: 30; */
}

.modal .containerkey {
  display: flex;
  margin: 0.25em 0;

  @media (min-width: 1081px) {
    flex-direction: row;
  }

  @media (max-width: 1080px) {
    flex-direction: column;
  }
}

.modalmobile {
  display: flex;
  flex-direction: column;
  background-color: var(--popup-background);
  background-image: var(--gradient-content);
  padding: 1.5vw 4vw;
  text-align: left;
  height: auto;
  position: fixed;
  overflow: auto;
  z-index: 20;
  cursor: default;

  transform: translateY(0%);
  opacity: 1;
  transition: transform 0.5s ease, opacity 0.1s ease;

  @media (min-width: 1081px) {
    top: 3%;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 1vw;
    max-height: 92vh;
    width: 100vw;
  }

  @media (max-width: 1080px) {
    top: auto;
    bottom: 4%;
    left: 1%;
    border-radius: 4vw;
    max-height: 90vh;
    width: 98vw;
  }
}

.modalmobile#hide {
  transform: translateY(100%);
  z-index: -100;
  opacity: 0;
  transition: transform 0.5s ease, opacity 0.1s ease;
}

@keyframes slideIn {
  from {
    transform: translateY(100%);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modalmobile .tabView {
  width: 100%;
  height: 70vh;
  overflow: scroll;
}

/* .modalmobile .tabView::-webkit-scrollbar {
  display: none;
} */

.modalmobile .buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 1vw;
}

.modalmobile .input {
  width: 75%;
  height: 1vw;
}

.modalmobile .input input {
  width: 100%;
  font-size: 1vw;
}

.modalmobile .title {
  width: 25%;
  height: 1vw;
  font-size: 1vw;
}

.modalmobile .containerkey {
  display: flex;
  margin: 0.25em 0;

  @media (min-width: 1081px) {
    flex-direction: row;
  }

  @media (max-width: 1080px) {
    flex-direction: column;
  }
}

.modalmobile .close:hover {
  cursor: pointer;
}

.frame {
  --b: 2px;
  /* thickness of the border */
  --c: black;
  /* color of the border */
  --w: 6vw;
  /* width of border */


  border: var(--b) solid #0000;
  /* space for the border */
  --_g: #0000 90deg, var(--c) 0;
  --_p: var(--w) var(--w) border-box no-repeat;
  background:
    conic-gradient(from 90deg at top var(--b) left var(--b), var(--_g)) 0 0 / var(--_p),
    conic-gradient(from 180deg at top var(--b) right var(--b), var(--_g)) 100% 0 / var(--_p),
    conic-gradient(from 0deg at bottom var(--b) left var(--b), var(--_g)) 0 100% / var(--_p),
    conic-gradient(from -90deg at bottom var(--b) right var(--b), var(--_g)) 100% 100% / var(--_p);

  /*Irrelevant code*/
  box-sizing: border-box;
  margin: 5px;
  display: inline-flex;
  font-size: 30px;
  justify-content: center;
  align-items: center;
  text-align: center;
}